<template>
    <div class="pledge">
        <div class="pledge-pool contianer">
            <div class="pledge-pool__title">股权奖励池</div>
            <el-row class="pledge-pool__list" :gutter="10">
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/1.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">总股权</div>
                        <div class="pledge-pool__list-desc">${{ status.totalStaking || 0 }}</div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/2.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">总奖励</div>
                        <div class="pledge-pool__list-desc">${{ isNaNHandler(Math.floor(status.totalRewards + status.totalStaking * 0.375)) || 0 }}</div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/3.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">已领取</div>
                        <div class="pledge-pool__list-desc">${{ status.totalReceive || 0 }}</div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/4.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">参与人数</div>
                        <div class="pledge-pool__list-desc">{{ status.userLength || 0 }}</div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/5.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">我的股权</div>
                        <div class="pledge-pool__list-desc">{{ status.userInfo.staking || 0 }}</div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/6.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">干股占比</div>
                        <div class="pledge-pool__list-desc">
                            {{
                                isNaNHandler(Math.floor(
                                ((status.userInfo.staking) / (status.totalStaking ) ) * 10000
                                ) / 100)
                            }}
                            %
                        </div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/7.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">分红占比</div>
                        <div class="pledge-pool__list-desc">
                            {{
                                isNaNHandler(Math.floor(
                                ((status.userInfo.staking + status.userInfo.receives) / (status.totalStaking + status.totalRewards) ) * 10000
                                ) / 100)
                            }}
                            %
                        </div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/8.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">如果复利占比</div>
                        <div class="pledge-pool__list-desc">
                            {{
                                isNaNHandler(Math.floor(
                                ((status.userInfo.staking + status.userInfo.receives+status.userInfo.rewards) / (status.totalStaking + status.totalRewards) ) * 10000
                                ) / 100)
                            }}
                            %
                        </div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/9.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">已领取</div>
                        <div class="pledge-pool__list-desc">${{ status.userInfo.rewards || 0 }}</div>
                    </div>
                </el-col>
                <el-col 
                    :xs="12" 
                    :sm="8" 
                    :md="8" 
                    :lg="6" 
                    :xl="6" 
                    class="flex-row-center align-center pledge-pool__list-item">
                    <img src="@/assets/images/pledge/10.png" class="pledge-pool__icon MR-20" />
                    <div>
                        <div class="pledge-pool__list-title">可领取</div>
                        <div class="pledge-pool__list-desc">${{ status.userInfo.receives || 0 }}</div>
                    </div>
                </el-col>
            </el-row>
            <div class="pledge-pool__footer">
                <el-button 
                    class="pledge-pool__btn" 
                    v-if="isLogin"
                    :loading="status.loading"
                    :disabled="status.userInfo.receives <= 0"
                    @click="receiveRewards"
                >
                    全部领取
                </el-button>
                <el-button class="pledge-pool__btn" v-else @click="loginFn">请连接钱包</el-button>
            </div>
        </div>
        
        <div class="contianer" v-if="isLogin">
            <div class="pledge-info flex-row-start">
                <el-progress class="pledge-info__progress" :width="160" type="dashboard" indeterminate stroke-linecap="square" :percentage="percentage" :stroke-width="14" color="#FCBE2F" />
                <div class="flex-1">
                    <div class="flex-row-start pledge-info__container">
                        <div class="flex-column-center align-center">
                            <div class="pledge-info__name">增加</div>
                            <div class="pledge-info__content">{{ isNaNHandler(Math.floor((status.balance * percentage) / 100)) }}</div>
                        </div>
                        <div class="flex-1 flex-column-center align-center">
                            <div class="pledge-info__name">获得</div>
                            <div class="pledge-info__content">$ {{ isNaNHandler(Math.floor((status.balance * percentage) / 100 / status.price)) }} / 股权币</div>
                        </div>
                        <div class="flex-column-center align-center">
                            <div class="pledge-info__name">余额</div>
                            <div class="pledge-info__content">{{ status.balance }} / TDAO </div>
                        </div>
                    </div>
                    <el-slider v-model="percentage" class="pledge-info__slider" />
                </div>
            </div>
            <div class="flex-row-center pledge-info__tools MB-40">
                <template v-if="isLogin && status.userInfo.invitation != status.zeroAdd">
                    <el-button
                        v-if="status.approve"
                        type="primary"
                        class="pledge-info__btn yellow"
                        :loading="status.loading"
                        :disabled="percent <= 0 || status.balance == 0"
                        @click="increaseStaking"
                    >
                        增加股权
                        
                    </el-button>
                    <el-button
                        v-else
                        class="pledge-info__btn yellow"
                        type="primary"
                        :loading="status.loading"
                        @click="doApprove"
                    >
                        授权
                    </el-button>
                </template>
                <template v-else-if="status.userInfo.invitation ==status.zeroAdd">
                    <input type="text" v-model="invitationText" placeholder="设置推荐人" class="pledge-info__input">
                    <div class="flex-row-start">
                        <el-button
                            class="pledge-info__btn yellow"
                            :loading="status.loading"
                            @click="doSetInvitation"
                        >
                            设置推荐人
                        </el-button>
                    </div>
                </template>
                <el-button class="pledge-info__btn" @click="copyLink">复制推广地址</el-button>
            </div>
        </div>

        <div class="contianer pledge-list" v-if="isLogin">
            <div class="pledge-list__nav flex-row-start">
                <div class="flex-1 text-center" @click="toggleType(1)">
                    <span class="pledge-list__nav-item pointer" :class="{'active': type == 1}">我的上级 ({{status.userInfo.invitation ? 1 : 0}})</span>
                </div>
                <div class="flex-1 text-center" @click="toggleType(2)">
                    <span class="pledge-list__nav-item pointer" :class="{'active': type == 2}">我的下级 ({{subordinateNum}})</span>
                </div>
            </div>
            <div class="flex-row-center" v-if="listLoading">
                <el-icon class="is-loading" :size="30"><Loading /></el-icon>
            </div>
            <template v-else>
                <el-empty v-if="list.length <= 0" description="暂无数据"/>
                <div class="pledge-list__item flex-row-between align-center MB-20" v-for="(item, index) in list" :key="index">
                    <div class="flex-row-start align-center">
                        <img src="@/assets/images/pledge/one.png" class="pledge-list__item-icon" v-if="index == 0"/>
                        <img src="@/assets/images/pledge/two.png" class="pledge-list__item-icon" v-else-if="index == 1"/>
                        <img src="@/assets/images/pledge/three.png" class="pledge-list__item-icon" v-else-if="index == 2"/>
                        <div class="pledge-list__item-num" v-else>{{index + 1}}</div>
                        <div class="pledge-list__item-addr">{{encryption(item.addr)}}</div>
                    </div>
                    <div class="flex-row-start align-center" v-if="type == 2">
                        <img src="@/assets/images/pledge/gold.png" class="pledge-list__item-gold"/>
                        <div class="pledge-list__item-price">+{{utils.formatUnits(item.share, 9)}}</div>
                        <div class="pledge-list__item-desc">返利</div>
                    </div>
                </div>
            </template>
            
        </div>
    </div>
</template>

<script>
import config from '../../plugins/wallet/config.json'
import STAKING_ABI from '../../api/abis/staking'
import ERC20_ABI from '../../api/abis/ERC20'
import UNISWAP_ABI from '../../api/abis/IUniswapV2'
import { utils, BigNumber } from 'ethers'
import { Tips } from '../../libs/utils'
import { ElNotification } from 'element-plus'
import { mapActions } from 'vuex'
export default {
    data() {
        return {
            utils,
            percentage: 10,
            // 是否连接到钱包
            isLogin: false,
            listLoading: true,
            subordinateNum: 0,
            list: [],
            type: 0,
            status: {
                totalStaking: 0,
                totalRewards: 0,
                totalReceive: 0,
                userLength: 0,
                userInfo: {
                address:"",
                receives: 0,
                rewards: 0,
                staking: 0,
                share:0,
                invitation:""
                },
                balance: 0,
                price: 0,
                stakingInput: 0,
                approve: false,
                loading: false,
                zeroAdd: "0x0000000000000000000000000000000000000000"
            }
        }
    },
    mounted() {
        this.isLogin = window.wallet.accounts[0] ? true : false
        this.isLogin && this.loginCallBack()
        window.wallet.on('accountChange', () => {
            this.isLogin = window.wallet.accounts[0] ? true : false
            this.isLogin && this.loginCallBack()
            if (!this.isLogin) {
                this.status = {
                    totalStaking: 0,
                    totalRewards: 0,
                    totalReceive: 0,
                    userLength: 0,
                    userInfo: {
                    address:"",
                    receives: 0,
                    rewards: 0,
                    staking: 0,
                    share:0,
                    invitation:""
                    },
                    balance: 0,
                    price: 0,
                    stakingInput: 0,
                    approve: false,
                    loading: false,
                    zeroAdd: "0x0000000000000000000000000000000000000000"
                }
            }
        })
    },
    methods: {
        ...mapActions(['setConnectDialog']),
        encryption(addr) {
            return addr.substr(0,5)+"***"+addr.substr(-6);
        },
        async increaseStaking() {
            this.status.loading = true;
            try {
                const increase = Math.floor((this.status.balance * this.percentage) / 100);
                const tx = await this.ContractStaking.increaseStaking(
                utils.parseEther(`${increase}`),
                { gasLimit: '1500000' }
                );
                await tx.wait();
                ElNotification({
                    type: 'success',
                    title: 'Sccess',
                    message: '股权增加成功'
                })
                this.loginCallBack();
            } catch (error) {
                Tips(error)
            }
            this.status.loading = false;
        },
        // 授权
         async doApprove() {
            this.status.loading = true;
            try {
                const tx = await this.ContractToken.approve(
                    this.ContractStaking.address,
                    utils.parseEther('10000000000000')
                );
                await tx.wait();
                this.status.approve = true;
                ElNotification({
                    type: 'success',
                    title: 'Sccess',
                    message: '授权完成'
                })
            } catch (error) {
                Tips(error)
            }
            this.status.loading = false;
        },
        toggleType(type) {
            if (this.type == type) {
                return
            }
            this.type = type
            if (type == 1) {
                this.list = this.status.userInfo.invitation ?  [{
                   addr: this.status.userInfo.invitation
                }] : []
            } else {
                this.getSubordinateList()
            }
        },
        loginFn() {
            this.setConnectDialog(true)
        },
        isNaNHandler(num) {
            return isFinite(num) ? num : 0
        },
        async loginCallBack() {
            this.ContractStaking = window.wallet.initContract(config[config.evn].STAKING, STAKING_ABI)
            this.ContractToken = window.wallet.initContract(config[config.evn].ERC20, ERC20_ABI)
            this.ContractUniswap = window.wallet.initContract(config[config.evn].UNISWAP, UNISWAP_ABI)
            const address = await this.ContractToken.signer.getAddress();
            const [
                totalStaking,
                totalRewards,
                totalReceive,
                userInfo,
                userLength,
                balance,
            ] = await Promise.all([
                this.ContractStaking.totalStaking(),
                this.ContractStaking.totalRewards(),
                this.ContractStaking.totalReceive(),
                this.ContractStaking.getUserInfo(address),
                this.ContractStaking.getUserLength(),
                this.ContractToken.balanceOf(address),
            ]);
            this.status.userLength = userLength.toNumber();
            this.status.balance = Math.floor(Number(utils.formatEther(balance)));
            this.status.totalStaking = this.toUnits(totalStaking);
            this.status.totalRewards = this.toUnits(totalRewards);
            this.status.totalReceive = this.toUnits(totalReceive);
            this.status.userInfo = {
                address:userInfo.addr,
                receives: this.toUnits(userInfo.receives),
                rewards: this.toUnits(userInfo.rewards),
                staking: this.toUnits(userInfo.staking),
                share: this.toUnits(userInfo.share),
                invitation:userInfo.invitation

            };
            // usdt -> wbnb -> this.ContractToken
            this.ContractUniswap.getAmountsOut(utils.parseEther('1'), [
                '0x55d398326f99059ff775485246999027b3197955',
                '0xbb4cdb9cbd36b01bd1cbaebf2de08d9173bc095c',
                config[config.evn].ERC20,
            ]).then(([USD, WETH, TDAO]) => {
                this.status.price = Math.floor(Number(utils.formatEther(TDAO)));
            });
            this.ContractToken.allowance(address, this.ContractStaking.address).then((allowance) => {
                const allow = Math.floor(Number(utils.formatEther(allowance)));
                this.status.approve = allow >= this.status.balance;
            });
            this.listLoading = true
            this.ContractStaking.getInvitationLength().then(data => {
                this.subordinateNum = data
                this.toggleType(1)
            }).finally(() => {
                this.listLoading = false
            })
        },
        // 获取下级列表
        getSubordinateList() {
            this.listLoading = true
            this.list = []
            this.ContractStaking.getInvitationList(BigNumber.from(0), this.subordinateNum).then(data => {
                this.list = data
                this.listLoading = false
            })
        },
        toUnits(num) {
            return Math.floor(Number(utils.formatUnits(num, 9)));
        },
        // 领取
        async receiveRewards() {
            this.status.loading = true;
            try {
                const tx = await this.ContractStaking.receiveRewards(
                utils.parseUnits(`${this.status.userInfo.receives}`, 9)
                );
                await tx.wait();
                ElNotification({
                    type: 'success',
                    title: 'Success',
                    message: '领取成功'
                })
                this.loginCallBack();
            } catch (error) {
                Tips(error)
            }
            this.status.loading = false;
        },
        async doSetInvitation() {
            if (!this.invitationText) {
                ElNotification({
                    type: 'error',
                    title: 'Error',
                    message: '请输入推荐人地址'
                })
                return
            }
            this.status.loading = true;
            try {
                const users = await this.ContractStaking.getUserInfo(this.invitationText);
                if(this.toUnits(users.staking) > 0 ){
                    const tx = await this.ContractStaking.setInvitation(
                        this.invitationText
                    );
                    await tx.wait();
                    ElNotification({
                        type: 'success',
                        title: 'Sccess',
                        message: '授权完成'
                    })
                }else{
                    ElNotification({
                        type: 'error',
                        title: 'Error',
                        message: '授权失败上级地址有误'
                    })
                }
                
            } catch (error) {
                Tips(error)
            }
            this.status.loading = false;
        },
        copyLink() {
            const text = document.location.href+"?addr="+ this.status.userInfo.address;
            // 添加一个input元素放置需要的文本内容
            const copyContent= document.createElement('input');
            copyContent.value = text;
            document.body.appendChild(copyContent);
            // 选中并复制文本到剪切板
            copyContent.select();
            document.execCommand('copy');
            // 移除input元素
            document.body.removeChild(copyContent);
            ElNotification({
                type: 'success',
                title: 'Sccess',
                message: '复制成功'
            })
        }
    }
}
</script>

<style lang="scss">
    .pledge-info__slider{
        .el-slider__bar{
            background: #1FD541;
        }
        .el-slider__button{
            border-color: #1FD541;
        }
    }
    
</style>
<style lang="scss" scoped>


@media only screen and (max-width: 900px) {
    .pledge-info{
        flex-direction: column;
        align-items: center;
        .pledge-info__progress{
            margin-right: 0 !important;
        }
    }
}
@media only screen and (max-width: 600px){
    .pledge-info__tools{
        align-items: center;
        margin-top: .2rem;
    }
    .pledge-info__container{
        align-items: center;
        flex-direction: column;
        padding: 20px 0.5rem !important;
    }
    .pledge-info__container>.flex-column-center{
        margin-bottom: .5rem;
    }
    .pledge-info__input{
        margin-bottom: 20px;
    }
    .pledge-list__item{
        padding: 0 .2rem !important;
        .pledge-list__item-num{
            margin-right: .2rem !important;
        }
        .pledge-list__item-icon{
            margin-right: 0.2rem !important;
        }
    }
}

.contianer{
    width: 100%;
    max-width: 1200px;
    margin: .2rem auto;
    border: 1px solid #2A2A2A;
    border-radius: .18rem;
}
.pledge{
    padding-bottom: .4rem;
}
.pledge-pool{
    .pledge-pool__title{
        border-bottom: 1px solid #2A2A2A;
        padding: .22rem 0;
        text-align: center;
        font-size: .22rem;
        font-weight: bold;
        color: #E8BE1B;
    }
    
    .pledge-pool__footer{
        border-top: 1px solid #2A2A2A;
        padding: .22rem 0;
        margin-top: .22rem;
        .pledge-pool__btn{
            background: #FCBE2F;
            border-color: #FCBE2F;
            color: #181818;
            font-size: .24rem;
            margin: 0 auto;
            width: 3rem;
            height: .5rem;
            background: #FCBE2F;
            border-radius: .18rem;
            display: block;
            font-weight: bold;
            &.is-disabled{
                opacity: .6;
            }
        }
    }
    .pledge-pool__list{
        padding: 0 .5rem;
        .pledge-pool__icon{
            width: .7rem;
            height: .7rem;
        }
        .pledge-pool__list-item{
            margin-top: .32rem;
        }
        .pledge-pool__list-title{
            font-size: .16rem;
            color: #fff;
            margin-bottom: .1rem;
            width: 1.5rem;
        }
        .pledge-pool__list-desc{
            font-size: .24rem;
            color: #E8BE1B;
            width: 1.5rem;
        }
    }
    
}
.pledge-info{
    padding: .3rem .6rem 0;
    .pledge-info__progress{
        width: 160px;
        height: 160px;
        margin-right: 1rem;
    }
    .pledge-info__container{
        padding: 10px .5rem;
    }
    .pledge-info__name{
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 20px;
    }
    .pledge-info__content{
        font-size: 20px;
        font-weight: bold;
        color: #FCBE2F;
    }
}
.pledge-info__input{
    width: 335px;
    height: 42px;
    background: #181818;
    border: 1px solid #2A2A2A;
    border-radius: 10px;
    padding: 0 20px;
    color: #fff;
    font-size: 14px;
}
.pledge-info__btn{
    width: 1.95rem;
    height: 42px;
    background: none;
    border-radius: 10px;
    border-color: #FCBE2F;
    color: #FCBE2F;
    &.yellow{
        background: #FCBE2F;
        color: #181818;
        margin-left: 20px;
    }
}
.pledge-list{
    padding: .4rem;
    .pledge-list__nav{
        font-size: .2rem;
        font-weight: bold;
        margin-bottom: .8rem;
        .pledge-list__nav-item{
            transition: all .3s;
        }
        .pledge-list__nav-item.active{
            position: relative;
            color: #F1B90E;
            &::before{
                position: absolute;
                bottom: -10px;
                width: 100%;
                left: 0;
                height: 2px;
                background: #F1B90E;
                content: "";
            }
        }
    }
}
.pledge-list__item{
